.App {
  min-height: 100vh;
  background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.searchForm {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.searchBar {
  width: 50%;
  border: none;
  padding: 10px;
  border-radius: 10px 0 0 10px;
}

.searchButton {
  background: lightcoral;
  border: none;
  padding: 10px 20px;
  color: white;
  border-radius: 0 10px 10px 0;
}

.recepies {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
