.recipe {
  border-radius: 10px;
  box-shadow: 0px 5px 20px rgb(46, 44, 44);
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: white;
  align-items: center;
  width: 40%;
}

.image {
  border-radius: 50%;
  width: 200px;
  height: 200px;
}

ol {
  list-style: none;
}
